.header-operations:after {
  display: inline-block;
  content: "";
  height: 100%;
  vertical-align: middle;
}
header:after {
  display: inline-block;
  content: "";
  height: 100%;
  vertical-align: middle;
}
html, body {
  height: 100%;
}
body {
  font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;
}
a {
  text-decoration: none;
}
header {
  height: 80px;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 0 20px;
  z-index: 1;
}
.container, .wrapper {
  height: 100%;
}
.wrapper {
  position: relative;
}
.container {
  padding-top: 80px;
}
.menu {
  height: 100%;
}
.content {
  padding: 25px;
}
.header-logo {
  display: inline-block;
  vertical-align: middle;
}
.header-operations {
  display: inline-block;
  float: right;
  padding-right: 100px;
  height: 100%;
}
.header-operations li {
  color: #fff;
  display: inline-block;
  vertical-align: middle;
  padding: 0 10px;
  margin: 0 10px;
  line-height: 80px;
  cursor: pointer
}
.header-operations li:last-child {
  cursor: default;
}
.header-operations span {
  opacity: 0.7;
}
.header-operations .header-lang {
  cursor: pointer
}
.header-operations .header-lang.is-active {
  opacity: 1;
}
.color-input::before {
  content: "#";
  position: absolute;
  left: 5px;
}
.color-input input {
  padding-left: 15px;
}
.color-buttons {
  float: right;
}



